import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ChqCasesTableComponent } from './chq-cases-table/chq-cases-table.component';
import { Route, RouterModule } from '@angular/router';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { MatIconModule } from '@angular/material/icon';
import { IconsModule } from 'src/app/icons.module';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';

const casesRoutes: Route[] = [
  { 
    path: '', 
    component: ChqCasesTableComponent,
    resolve: { version: VersionControlResolver },
  }
];

@NgModule({
  declarations: [ ChqCasesTableComponent ],
  imports: [
    CommonModule,
    MatIconModule,
    IconsModule,
    RouterModule.forChild(casesRoutes),
    WidgetsModule,
  ],
})
export class ChqCasesModule {}
